      <div class="IBUBeersTable__beerIBU">{{ beer.minIBU }} - {{ beer.maxIBU }}</div>
      <div class="IBUBeersTable__beerIBULine">
        <div
          class="IBUBeersTable__beerIBUPoint"
          v-for="index in 100"
