<template>
  <div class="IBUCalculator">
    <h3>1. Combien de litre de bière ?</h3>
    <div class="Input">
      <span>Mon brassin fait <input type="number" id="beerL" v-model="$store.state.beerL" onClick="this.select();" placeholder="20">L de bière</span>
    </div>
    <h3>2. Quelle densité initiale (après ébulition) ?</h3>
    <div class="Input">
      <span>La densité initiale est de <input type="number" id="beerL" v-model="$store.state.densite" onClick="this.select();" placeholder="1051"></span>
    </div>

    <h3>3. Quels houblons sont utilisés ?</h3>
    <table class="IBUCalculator__table">
      <tr>
        <!-- <th>Nom du malt</th> -->
        <th></th>
        <th>Qt. de houblon (en g)</th>
        <th>Teneur en acide α (en %)</th>
        <th>Temps d'ébulition (en min)</th>
        <th>Type de houblon</th>
        <th>Utilisation</th>
        <th>IBU</th>
      </tr>
      
      <tr v-for="(hop, key) in hops" :key="key">
        <td><button v-show="hop.length >= 2" @click="destroyHop(key)">x</button>
        <td><input type="number" step="1" id="maltQt" v-model="hop.hopQt" placeholder="ex: 120"></td>
        <td><input type="number" id="IBU" v-model="hop.alpha" placeholder="ex: 14"></td>
        <td><input type="number" id="time" v-model="hop.time" placeholder="ex: 60"></td>
        <td><input type="number" id="type" v-model="hop.type" placeholder="ex: 14"></td>
        <td>{{ Math.round(getUtilisation(hop) * 1000) / 10 }} %</td>
        <td>{{ Math.round(getIBU(hop) * 10) / 10 }}</td>
      </tr>
      <tr>
        <td></td>
        <td colspan="4"><button @click="addHop()" class="Add">+ Ajouter un autre type de houblon <img :src="require('../assets/ibu.svg')"/></button></td>
      </tr>
    </table>
    <!-- <p style="height: 0;overflow:hidden;">Total MCU : {{ totalMCU }}</p> -->
    <p style="">Total IBU : {{ totalIBU }}</p>
  </div>
</template>

<script>

export default {
  name: 'IBUCalculator',
  data: function () {
    return {
      hops: [{alpha:null,hopQt:null,time:null,type:null}],
    }
  },
  computed: {
    // totalMCU: function () {
    //   let totalMCU = 0;
    //   this.hops.forEach(malt => { totalMCU += this.getMCU(malt) })
    //   return Math.round(totalMCU * 10) / 10;
    // },
    totalIBU: function () {
      let totalIBU = 0;
      this.hops.forEach(hop => { totalIBU += this.getIBU(hop) })
      let ibu = Math.round(totalIBU)
      this.$store.commit ('updateIBU', ibu)
      return ibu;
    },
  },
  methods: {
    // getMCU(malt) {
    //   return Math.round((4.24 * malt.IBU * (malt.maltQt / 1000) / this.$store.state.beerL) * 10) / 10
    // },
    getCorrectionDensite() {
      return 1 + (this.$store.state.densite/1000 - 1.050) / 2
    },
    getUtilisation(hop) {
      return 1.65 * Math.pow(0.000125, ((this.$store.state.densite/1000)-1)) * ((1 - Math.exp(-0.04 * hop.time)) / 4.15)
    },
    getIBU (hop) {
      return (this.getUtilisation(hop) * hop.hopQt * hop.alpha * 10) / (this.$store.state.beerL * this.getCorrectionDensite())
    },
    addHop() {
      this.hops.push({alpha:null,hopQt:null,time:null,type:null})
    },
    destroyHop(id) {
      this.hops.splice(id, 1);
    },
    // getHexIBU() {
    //   var IBU2hex = new IBU2Hex;
    //   return IBU2hex.convert(this.totalIBU, 5);
    // },
    convert() {
      let IBU = this.totalIBU;
      let sat = 1
        IBU = IBU > 80 ? 80 : IBU;
        IBU = IBU < 0 ? 0 : IBU;
        var srm = this.IBUToSrm(IBU);
        var rgb = {
            r: this.calcRed(srm),
            g: this.calcGreen(srm),
            b: this.calcBlue(srm)
        };
        rgb = this.desaturate(rgb, sat);
        
        return this.rgbToHex(rgb);
    },
    IBUToSrm: function(IBU) {
        return IBU * 0.508;
    },
    calcRed: function(srm) {
        var result = Math.round(280 - srm * 5.65);
        return result > 255 ? 255 : result;
    },
    calcGreen: function(srm) {
        var result = Math.round(0.188349 * Math.pow(srm, 2) - 13.2676 * srm + 239.51);
        return result;
    },
    calcBlue: function(srm) {
        var result = Math.round(0.000933566 * Math.pow(srm, 4) - 0.0894788 * Math.pow(srm, 3) + 3.00611 * Math.pow(srm, 2) - 40.8883 * srm + 183.409);
        return result < 0 ? 0 : result;
    },
    segmentToHex: function(seg) {
        var hex = seg.toString(16);
        return hex.length == 1 ? "0" + hex : hex;
    },
    rgbToHex: function(rgb) {
        return "#" + this.segmentToHex(rgb.r) + this.segmentToHex(rgb.g) + this.segmentToHex(rgb.b);
    },
    desaturate: function(rgb, sat) {
        var gray = rgb.r * 0.3086 + rgb.g * 0.6094 + rgb.b * 0.0820;
        gray = gray * (1 - sat);
        rgb.r = Math.round(rgb.r * sat + gray);
        rgb.g = Math.round(rgb.g * sat + gray);
        rgb.b = Math.round(rgb.b * sat + gray);
        return rgb;
    }
  }
  // components: {
  //   IBUMaltRow
  // },
}
</script>

